.docrevenue_total {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 15px;
  font-size: 1.5rem;
  font-family: "Roboto" !important;
  color: #333;
}
.totalkwd {
  background-color: rgb(130, 174, 63);
  width: 32%;
  padding: 13px;
  color: #fff;
  border-radius: 2px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  box-shadow: 0px 2px 4px 0.5px rgba(0, 0, 0, 0.13),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}
.totalkwd p {
  margin-bottom: 0px !important;
}
.grand_total {
  font-size: 1.75rem;
  margin-right: 5px;
}
.revtotal_amt {
  font-size: 1.5rem;
  position: relative;
  top: 2px;
}
.group_grandtotal{
  display: flex;
  align-items: flex-end;
  border: 1PX solid;
  background-color:#82ae3f;
  font-size: 18PX;
  padding: 15PX;
  width: 235px;
  color: white;
  float: right;
  margin-top:50px;
  margin-right: 15px;
}